import fetch from './config'
export default {

    /**
     * 
     * @param {object} params 
     * @returns 
     */
    banner(params) {
        return fetch.fetchPost('News/banner', params);
    },

    /**
     * 
     * @param {oeject} params 
     * @returns 
     */
    joke(params) {
        return fetch.fetchGet('/joke', params);
    },

    /**
     * 
     * @param {oeject} params 
     * @returns 
     */
    joke_pic(params) {
        return fetch.fetchGet('/joke_pic', params);
    },

    /**
     * 
     * @param {object} params {pagetype:}
     * @returns 
     */
    new_list(params) {
        return fetch.fetchGet('/news_list?type=0&page=10', params)
    },

    /**
     * 
     * @param {object} params {postid ID}
     * @returns 
     */
    article(params) {
        return fetch.fetchGet('News/new_detail', params)
    },

    /**
     * 
     * @param {object} params {ps:pagesize}
     * @returns 
     */
    WxUrl(params) {

        return fetch.fetchGet('News/wx_select', params)
    },

    /**
     * 
     * @param {object} params {page:10 20 30 ,type:0,1,2}
     * @returns 
     */
    video_type(params) {
        return fetch.fetchGet('/video_list', params)
    },

    /**
     * 
     * @param {any} params 
     * @returns 
     */
    photo(params) {
        return fetch.fetchGet('/huaban', params);
    },

    /**
     * 
     * @param {object} params 
     * @returns 
     */
    playing_movie_list(params) {
        return fetch.fetchGet('Movie/playing_movie_list', params);
    },

    /**
     * 
     * @param {object} params 
     * @returns 
     */
    movie_info(params) {
        return fetch.fetchGet('Movie/movie_info', params);
    },

    /**
     * 
     * @returns 
     */
    zhihu_daily() {
        return fetch.fetchGet('Zhihu/zhihu_daily');
    },
    /**
     * 
     * @param {object} params  {id:id}
     * @returns 
     */
    zhihu_daily_info(params) {
        return fetch.fetchGet('/Zhihu/news', params);
    },

    /**
     * 
     */
    weather_forecast(params) {
        return fetch.fetchGet('/web_frame',params)
    }
}


